          <div class="copy-field">
            <el-input 
              value="https://api.baiyi-pay.com" 
              readonly 
              class="copy-input"
            />
            <el-button 
              type="primary" 
              size="small" 
              @click="copyToClipboard('https://api.baiyi-pay.com', 'API网关')"
            >
              复制
            </el-button>
          </div>
        </div>

        <div class="endpoint-item">
