  </transition>

  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新、添加、编辑、删除和导入导出按钮 -->
    <el-button v-permission="'merchant:list'" :icon="Refresh" @click="refreshData" color="#40485b"/>
    <el-button v-permission="'merchant:add'" type="primary" :icon="Plus" @click="openEditFrom">添加</el-button>
    <el-button v-permission="'merchant:edit'" type="primary" :disabled="!hasSelectedRows" @click="handleBatchEdit">编辑</el-button>
    <el-button v-permission="'merchant:delete'" type="danger" :disabled="!hasSelectedRows" @click="handleBatchDelete('/api/v1/admin/merchant/delete')">删除</el-button>
    <el-button v-permission="'merchant:import'" type="primary">
      导入<el-icon class="el-icon--right"><Upload /></el-icon>
    </el-button>
    <el-button v-permission="'merchant:export'" type="primary">
