      <el-table-column type="selection" width="40" />
      <el-table-column prop="merchant_name" label="商户名称" align="center" min-width="150"/>
      <el-table-column prop="login_account" label="登录账号" align="center" min-width="120"/>
      
      <!-- 金额信息 -->
      <el-table-column label="金额信息" align="center" min-width="200">
        <el-table-column prop="withdrawable_amount" label="可提金额" width="100" align="center">
          <template #default="{ row }">
            {{ formatAmount(row.withdrawable_amount) }}元
          </template>
        </el-table-column>
        <el-table-column prop="frozen_amount" label="冻结金额" width="100" align="center">
          <template #default="{ row }">
            {{ formatAmount(row.frozen_amount) }}元
          </template>
        </el-table-column>
      </el-table-column>

      <!-- 预付信息 -->
      <el-table-column label="预付信息" align="center" min-width="200">
        <el-table-column prop="prepayment_total" label="预付总额" width="100" align="center">
          <template #default="{ row }">
            {{ formatAmount(row.prepayment_total) }}元
          </template>
        </el-table-column>
        <el-table-column prop="prepayment_remaining" label="剩余预付" width="100" align="center">
          <template #default="{ row }">
            {{ formatAmount(row.prepayment_remaining) }}元
          </template>
        </el-table-column>
      </el-table-column>




      <!-- 状态列 -->
      <el-table-column label="状态" align="center" min-width="80">
        <template #default="{ row }">
          <el-switch 
            v-permission="'merchant:toggle_status'"
            v-model="row.status" 
            :active-value="1" 
            :inactive-value="0" 
            @change="toggleSwitch('/api/v1/admin/merchant/status-switch', row.id)"
          />
        </template>
      </el-table-column>

      <!-- 创建时间列 -->
      <el-table-column prop="created_at" label="创建时间" align="center" min-width="160"/>

      <!-- 操作列 -->
      <el-table-column label="操作" align="center" fixed="right" min-width="360">
        <template #default="scope">
          <el-button v-permission="'merchant:edit'" size="small" @click="openEditFrom(scope.row)">编辑</el-button>
          <el-button v-permission="'merchant:api_docs'" size="small" type="info" @click="openApiDocs(scope.row)">对接资料</el-button>
          <el-button v-permission="'merchant:product_management'" size="small" type="success" @click="openProductManagement(scope.row)">产品管理</el-button>
          <el-button v-permission="'merchant:delete'" size="small" type="danger" @click="handleDelete('/api/v1/admin/merchant/delete', scope.row)">删除</el-button>
