<script lang="ts" setup>
import { Plus, Refresh, Upload, Download, Search, Close } from '@element-plus/icons-vue';
import { ref, onMounted, defineAsyncComponent } from 'vue';
import { ElMessage } from 'element-plus';

// 导入编辑弹窗
const EditForm = defineAsyncComponent(() => import('./edit.vue'));
const editFormRef = ref<any>(null);
const openEditFrom = (row: any = {}) => {
  console.log('🔍 openEditFrom called', { 
    row, 
    editFormRef: editFormRef.value,
    hasEditFormRef: !!editFormRef.value 
  });
  
  if (editFormRef.value) {
    console.log('✅ Calling editFormRef.value.open');
    editFormRef.value.open({ ...row });
  } else {
    console.error('❌ editFormRef.value is null - 组件可能未正确加载');
  }
};

// 导入产品管理弹窗
const ProductManagement = defineAsyncComponent(() => import('./product-management.vue'));
const productManagementRef = ref<any>(null);
const openProductManagement = (row: any) => {
  if (productManagementRef.value) {
    productManagementRef.value.open({
      id: row.id,
      merchant_name: row.merchant_name
    });
  }
};

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 使用表格插件来管理表格的状态、数据等
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams,
  deleteRows,
  toggleSwitch
} = useTableDataPlugin('/api/v1/admin/merchant');

// 搜索表单数据
const searchForm = ref({
  login_account: '',
  status: ''
});

// 搜索加载状态
const searchLoading = ref(false);

// 处理搜索
const handleSearch = async () => {
  searchLoading.value = true;
  try {
    // 将搜索表单数据同步到searchParams
    searchParams.value = { ...searchForm.value };
    await refreshData();
  } catch (error) {
    console.error('搜索失败:', error);
    ElMessage.error('搜索失败，请重试');
  } finally {
    searchLoading.value = false;
  }
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    login_account: '',
    status: ''
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
};

// 金额格式化：分转元
const formatAmount = (amount: number) => {
  return (amount / 100).toFixed(2);
};


// 对接资料对话框
const apiDocsVisible = ref(false);
const currentMerchant = ref<any>(null);

// 打开对接资料对话框
const openApiDocs = (merchant: any) => {
  currentMerchant.value = merchant;
  apiDocsVisible.value = true;
};

// 复制到剪贴板
const copyToClipboard = async (text: string, label: string) => {
  try {
    await navigator.clipboard.writeText(text);
    ElMessage.success(`${label}已复制到剪贴板`);
  } catch (err) {
    ElMessage.error('复制失败');
  }
};

// 在组件挂载时加载数据
onMounted(() => {
  loadData();
});

// 批量编辑
const handleBatchEdit = () => {
  // 这里可以实现批量编辑逻辑
  console.log('批量编辑');
};

// 单条删除
const handleDelete = (api: string, row: any) => {
  deleteRows(api, [row.id]);
};

// 批量删除
const handleBatchDelete = (api: string) => {
  deleteRows(api);
};


</script>

